.Headerbar-Container {
  --top-offset: -25px;
  --max-height-headerbar: 90px;
  --background-color-headerbar: #1e1e1ef2;
  --borderstyle-headerbar: 2px solid white;
}

.Headerbar {
  background-color: var(--background-color-headerbar);
  border-bottom: var(--borderstyle-headerbar);
  top: var(--top-offset);
  width: 100vw;
  z-index: 999;
  max-height: var(--max-height-headerbar);
  grid-template-columns: 100vw;
  grid-auto-rows: 70px;
  justify-items: center;
  padding-top: 20px;
  transition: max-height .5s ease-in-out;
  display: grid;
  position: fixed;
  overflow: hidden;
  box-shadow: 0 0 10px 15px #00000080;
}

.Headerbar::-webkit-scrollbar {
  width: 0;
}

.Headerbar__Link {
  color: #ccc;
  text-decoration: none;
}

.Headerbar__Filler {
  display: none;
}

.Headerbar__Button {
  cursor: pointer;
  width: 90%;
  border-bottom: 1px solid #ffffff3f;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition: transform .1s;
  display: flex;
}

.Headerbar .Headerbar__Button:last-child {
  border: 0;
}

.Headerbar__Button:hover {
  z-index: 100;
  transform: scale(1.01);
}

.Headerbar__Label {
  text-transform: uppercase;
  text-align: left;
  user-select: none;
  font-size: 3rem;
}

.Headerbar__Logo-And-Menu {
  width: 100%;
  border-bottom: 1px solid #fff;
  flex-direction: row;
  order: -1;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.Headerbar__Logo {
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  display: flex;
}

.Logo__Text {
  user-select: none;
  color: var(--header-color);
  margin: 0;
  font-size: 2.5rem;
  font-weight: 600;
  text-decoration-color: #fff;
}

.Button__Menu {
  width: fit-content;
  border: 0;
}

@media screen and (min-width: 600px) {
  .Headerbar__Logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .Button__Menu {
    position: absolute;
    right: 0;
  }
}

.Headerbar--ausgeklapt {
  max-height: calc(100vh - var(--top-offset) );
  overflow-y: scroll;
}

@media screen and (min-width: 1250px) {
  .Headerbar {
    --max-height-headerbar: 150px;
    flex-direction: row;
    align-items: flex-end;
    padding-right: 10px;
    display: flex;
    top: -55px;
  }

  .Headerbar__Label {
    font-size: 1.5rem;
    font-weight: 500;
  }

  .Headerbar__Filler {
    display: block;
  }

  .Headerbar__Button {
    width: fit-content;
    border: 0;
    flex-direction: column;
    position: relative;
    bottom: 10px;
  }

  .Headerbar__Button:hover {
    z-index: 100;
    transform: scale(1.1);
  }

  .Headerbar__Icon {
    transform: scale(.6)translateY(25px);
  }

  .Headerbar__Button:not(.Button__Home):not(.Button__Products) .Headerbar__Icon {
    transform: scale(.45)translateY(60px);
  }

  .Headerbar__Logo-And-Menu {
    width: fit-content;
    border: 0;
    order: 0;
  }

  .Headerbar__Logo-And-Menu .Button__Menu {
    display: none;
  }

  .Headerbar__Logo {
    flex-direction: column;
    align-items: center;
    transform: translateY(-55px)translateX(-50%);
  }

  .BM_Logo {
    transform: translateY(14px);
  }

  .Logo__Text {
    font-size: 2rem;
  }
}

.Submenu {
  background-color: var(--background-color-headerbar);
  border: var(--borderstyle-headerbar);
  --border-radius-submenu: 5px;
  border-radius: 0 0 var(--border-radius-submenu) var(--border-radius-submenu);
  --default-submenu-color: #ccc;
  z-index: 10000000;
  max-height: 0;
  scrollbar-width: thin;
  scrollbar-color: white;
  transform-origin: 50% 0;
  border-top: none;
  flex-direction: column;
  padding: 0 0 10px;
  list-style: none;
  transition: max-height .5s;
  display: flex;
  position: absolute;
  overflow-y: auto;
  box-shadow: -10px 10px 10px 5px #000000b3;
}

.Submenu--expanded {
  max-height: 80vh !important;
}

.Submenu::-webkit-scrollbar {
  width: 2px;
}

.Submenu::-webkit-scrollbar-thumb {
  background-color: #fff;
}

.Submenu::-webkit-scrollbar-track {
  background-color: #858585;
}

.Submenu__Category {
  color: var(--default-submenu-color);
  cursor: pointer;
  margin: 10px 0;
  padding: 0 20px;
  font-weight: 500;
}

.Submenu__Category:not(.Submenu__Category:first-child) {
  border-top: 1px solid #ffffff4d;
  padding-top: 10px;
}

.Submenu__Category:hover, .Submenu__Item:hover {
  color: #fff;
  cursor: pointer;
}

.Submenu__Item {
  color: var(--default-submenu-color);
  margin-right: 2rem;
  padding-left: 40px;
}

.Submenu--Products {
  margin-top: 0;
}

.Submenu input[type="text"], .Submenu input[type="password"] {
  color: #fff;
  background-color: #3c3c3c;
  border: none;
  border-radius: 1000px;
  outline: none;
  margin: 10px;
  padding: 1.5rem 2rem;
  transition: all .3s;
}

.Submenu input[type="text"]::placeholder, .Submenu input[type="password"]::placeholder {
  color: #aaa;
}

.Submenu input[type="text"]:hover, .Submenu input[type="password"]:hover {
  background-color: #5a5a5a;
  box-shadow: inset 0 0 1px 1px #fff;
}

.Submenu input[type="text"]:hover::placeholder, .Submenu input[type="password"]:hover::placeholder {
  color: #111;
}

.Submenu input[type="submit"] {
  cursor: pointer;
  border-radius: 1000px;
  margin: auto;
  padding: 1.5rem 2rem;
}

a {
  color: inherit;
  text-decoration: inherit;
